.statistics {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    text-align: center;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
    color: black;
    gap: 20px;    
}

.title {
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 600;
    margin: 0;
}
.statList {
display: flex;
align-items: center;
justify-content: center;
list-style: none;
margin: 0;
padding: 0;
font-size: 18px;

}
.item {
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
flex-direction: column;
min-width: 80px;
font-size: 18px;
padding: 15px;
color:white;
background-color: brown;
}
